<template>
    <div>
        <ProfileBanner />
        <div class="container" ref="bodyloady">
            <div class="row">

                <LeftSideInfo />
                <!-- <RightSideInfo /> -->
                <CenterSideInfo />

            </div>
        </div>
        <Footer />
    </div>
</template>

<script>
    import ProfileBanner from "../../components/Profile/ProfileBanner";
    import LeftSideInfo from "../../components/Profile/LeftSideInfo";
    import RightSideInfo from '../../components/Home/RightSideInfo';
    import CenterSideInfo from "../../components/Profile/CenterSideInfo";
    import Footer from '../../components/Home/Footer';
    import ls from "../../localstorage";


    export default {
        name: "Profile",
        data() {
            return {
                signedIn: false,
            }
        },
        components: { LeftSideInfo, RightSideInfo, CenterSideInfo, Footer, ProfileBanner },
        watch: {
            $route(to) {
                var path = to;
                var username = this.$route.params.username;
                this.checkUserName(username);
            }
        },
        mounted() {
            var username = this.$route.params.username;
            this.checkUserData();
            this.checkUserName(username);
            $("title").html( "@" + username +  " / " + this.$store.state.projectName);
        },
        methods: {
            showLoading() {
                let loader = this.$loader(this, this.$refs.bodyloady);
                return loader;
            },
            checkUserData() {
                let userData = ls.getStorage("fansData");
                if (userData !== "" || userData !== "undefined") {
                    this.signedIn = true;
                }
            },
            checkUserName(username) {
                let user;
                if (ls.checkStorage("fansData")) {
                    let userData = ls.getStorage("fansData");
                    user = userData[0].username;
                }else {
                    user = '';
                }
                // console.log(username)
                if (user !== username) {
                    this.axios.get("/user/username/check/" + username)
                        .then(response => {
                            // console.log(response.data.data.status);
                            let status = response.data.data.status;
                            // if username does not exist move to 404 page
                            if (status === false) {
                                this.$router.push("/404");
                            }else {
                                // else get user data
                                // and set data respectively
                                window.eventBus.$emit("isOtherUser", true);
                            }
                        })
                        .catch(error => {
                            // console.log(error);
                            return error;
                        })
                }else {
                    window.eventBus.$emit("isOtherUser", false);
                }
            },
        },
    };
</script>

<style>
    .top-header-thumb img {
        border-radius: 0 !important;
    }
    .ui-block {
        border: none !important;
    }
    .your-profile-menu {
        padding: 0 25px;
    }
</style>
