<template>
  <!-- Main Content -->

  <div
    class="col col-xl-6 order-xl-2 col-lg-12 order-lg-1 col-md-12 col-sm-12 col-12"
    ref="newsfeeds"
  >
    <div id="newsfeed-items-grid">
      <span style="width: 100%" v-if="allReviews.length > 0">
        <span style="width: 100%" v-for="(feed, index) in allReviews" :key="index">
          <div class="ui-block">
            <!-- Post -->

            <article class="hentry post">
              <div class="post__author author vcard inline-items">
                <span v-if="feed.user_info.type === 1">
                  <img :src=" feed.user_info.avatar === null ? reviewUserAvatar(feed.user_info.username) : feed.user_info.avatar" alt="author" />
                </span>
                <span v-else>
                  <img :src=" feed.user_info.logo === null ? reviewUserAvatar(feed.user_info.business_name) : feed.user_info.logo" alt="author" />
                </span>

                <div class="author-date">
                  <a
                          class="h6 post__author-name fn"
                          :href="'/profile/' + feed.user_info.username"
                  >
                      <span v-if="feed.user_info.type === 1">
                          {{ feed.user_info.first_name === null || feed.user_info.first_name === '' ? feed.user_info.username : feed.user_info.first_name + ' ' + feed.user_info.last_name }}
                      </span>
                      <span v-if="feed.user_info.type === 2">
                          {{ feed.user_info.business_name === null ? feed.user_info.username : feed.user_info.business_name }}
                      </span>
                  </a>
                  Made a review on
                  <router-link :to="{ name: 'Match', params: {fixture_id: feed.fixture_id } }">{{getFixtures(feed.fixture.home_team_name,feed.fixture.away_team_name)}}</router-link>
                  <div class="post__date">
                    <time
                      class="published"
                      :datetime="feed.review_time"
                    >{{ timeAgoFunction(feed.review_time) }}</time>
                  </div>
                </div>
              </div>

              <p>{{feed.review}}</p>

              <span style="width: 100%" v-if="feed.images.length > 0">
                <div class="post-thumb" v-for="(images, index) in feed.images" :key="index">
                  <img :src="images.src" alt="photo" />
                </div>
              </span>

              <div class="post-additional-info inline-items">
                <a href="javascript:;" class="post-add-icon inline-items">
                  <svg class="olymp-heart-icon">
                    <use xlink:href="/svg-icons/sprites/icons.svg#olymp-heart-icon" />
                  </svg>
                  <span>{{ feed.likes_count }}</span>
                </a>

<!--                <ul v-if="feed.likes_count > 0" class="friends-harmonic">-->
<!--                  <li>-->
<!--                    <a href="#">-->
<!--                      <img src="/img/friend-harmonic7.jpg" alt="friend" />-->
<!--                    </a>-->
<!--                  </li>-->
<!--                  <li>-->
<!--                    <a href="#">-->
<!--                      <img src="/img/friend-harmonic8.jpg" alt="friend" />-->
<!--                    </a>-->
<!--                  </li>-->
<!--                  <li>-->
<!--                    <a href="#">-->
<!--                      <img src="/img/friend-harmonic9.jpg" alt="friend" />-->
<!--                    </a>-->
<!--                  </li>-->
<!--                  <li>-->
<!--                    <a href="#">-->
<!--                      <img src="/img/friend-harmonic10.jpg" alt="friend" />-->
<!--                    </a>-->
<!--                  </li>-->
<!--                  <li>-->
<!--                    <a href="#">-->
<!--                      <img src="/img/friend-harmonic11.jpg" alt="friend" />-->
<!--                    </a>-->
<!--                  </li>-->
<!--                </ul>-->

<!--                <div v-if="feed.likes_count > 0" class="names-people-likes">-->
<!--                  <a href="#">Jenny</a>,-->
<!--                  <a href="#">Robert</a> and-->
<!--                  <br />6 more liked this-->
<!--                </div>-->

                <div class="comments-shared">
                  <a href="javascript:;" class="post-add-icon inline-items">
                    <svg class="olymp-speech-balloon-icon">
                      <use xlink:href="/svg-icons/sprites/icons.svg#olymp-speech-balloon-icon" />
                    </svg>
                    <span>{{ feed.comments_count }}</span>
                  </a>
                </div>
              </div>
            </article>

            <ul v-if="feed.comments_count > 0" class="comments-list hide">
              <li v-for="(comment, index) in feed.comments" :key="index" class="comment-item">
                <div
                  v-for="(userDetail, index) in comment.comment.user"
                  :key="index"
                  class="post__author author vcard inline-items"
                >
                  <span v-if="userDetail.avatar === null">
                    <img :src="reviewUserAvatar(userDetail.username)" alt="author" />
                  </span>
                  <span v-else>
                    <img :src="userDetail.avatar" alt="author" />
                  </span>

                  <div class="author-date">
                    <a
                      class="h6 post__author-name fn"
                      href="#"
                    >{{ userDetail.first_name !== null ? userDetail.first_name + ' ' + userDetail.last_name : userDetail.username }}</a>
                    <div class="post__date">
                      <time
                        class="published"
                        :datetime="comment.comment.time"
                      >{{ $timeAgo.format(new Date(comment.comment.time * 1000)) }}</time>
                    </div>
                  </div>
                </div>

                <p>{{ comment.comment.comment }}</p>
              </li>
            </ul>

            <!-- .. end Post -->
          </div>
        </span>
      </span>
      <div v-else class="ui-block">
        <div
          class="ui-block-content text-center"
          style="padding: 50px 23px; border-top:solid 4px #08ddc1"
        >
          <h4 class="title" style="font-weight:bold">Fill Out Your Profile</h4>
          <p>
            Add photos and info to your profile so people can find you
            <br />easily and get to know you better!
          </p>
        </div>
      </div>
    </div>
  </div>

  <!-- ... end Main Content -->
</template>

<script>
var pageData = {
  allReviews: {},
  photoReview: {},
  username: "",
  firstname: "",
  lastname: "",
  avatar: "",
    signedIn: false,
};

export default {
  name: "Newsfeeds",
  data() {
    return pageData;
  },
  watch: {
    $route(to) {
      this.getAllReviews();
      return to;
    }
  },
  mounted() {
	this.getAllReviews();
	window.eventBus.$on("reviews", val => {
		// console.log(val)
      return val;
	});
  },
  methods: {
    timeAgoFunction(timestamp) {
      return this.$timeAgo.format(new Date(timestamp * 1000));
    },
    reviewUserAvatar(avatar) {
      var uname = avatar;
      var immmg = uname
        ? `/avatars/av${uname.length - (8 % 3) + 1}.png`
        : "/avatars/av1.png";
      return immmg;
    },
    getFixtures(name1,name2) {
      return name1 + " (VS) " + name2;
    },
      checkUserData() {
          let userData = ls.getStorage("fansData");
          if (userData !== "" || userData !== "undefined") {
              this.signedIn = true;
          }
      },
    getAllReviews() {
      let usernameRoute = this.$route.params.username;
      if (this.signedIn === false) {
          this.axios
              .get("/other/detailsout/" + usernameRoute + "/20")
              .then(response => {
                  var responseData = response.data.data;
                  // console.log("profile reviews",responseData);
                  this.allReviews = responseData.reviews.data;
                  this.username = responseData.username;
                  this.firstname = responseData.firstname;
                  this.lastname = responseData.lastname;
                  this.avatar = responseData.avatar;
              })
              .catch(error => {
                  //   console.log(error);
                  return error;
              });
      }else {
          this.axios
              .get("/other/detailsin/" + usernameRoute + "/20")
              .then(response => {
                  var responseData = response.data.data;
                  // console.log("profile reviews",responseData);
                  this.allReviews = responseData.reviews.data;
                  this.username = responseData.username;
                  this.firstname = responseData.firstname;
                  this.lastname = responseData.lastname;
                  this.avatar = responseData.avatar;
              })
              .catch(error => {
                  //   console.log(error);
                  return error;
              });
      }
	},
	getPhotoReviews() {
      var usernameRoute = this.$route.params.username;
      this.axios
        .get("/other/details/" + usernameRoute  + "/20")
        .then(response => {
          var responseData = response.data.data;
          this.photoReview = responseData.image_reviews;
          this.username = responseData.username;
          this.firstname = responseData.firstname;
          this.lastname = responseData.lastname;
          this.avatar = responseData.avatar;
        //   console.log(responseData);
        })
        .catch(error => {
          // console.log(error);
          return error
        });
    }
  }
};
</script>

<style>
.ui-block-title {
  border-bottom: none;
}
.fw-1 {
  width: 100%;
}
.post-additional-info {
  border-top: none;
  padding: 0;
}
.blog-post .post-content {
  padding: 20px 0;
}
</style>
