<template>
  <div
    class="col col-xl-3 order-xl-1 col-lg-6 order-lg-2 col-md-6 col-sm-6 col-12 d-none d-sm-none d-md-block"
  >
    <div class="ui-block">
      <div class="your-profile">
        <div class="ui-block-title ui-block-title-small">
          <h6 class="title">Your PROFILE</h6>
        </div>
        <div class="card">
          <ul class="your-profile-menu">
            <li v-if="city === null ">
              <router-link :to="{name: 'AccountInfo'}">
                <span style="font-size:20px;font-weight:normal;position:relative;top:2px">+</span> Add your current city
              </router-link>
            </li>
            <li>
              <a>
                <span>
                  <svg
                    style="width:16px;height:16px;position:relative;top:3px"
                    class="olymp-chat---messages-icon"
                  >
                    <use xlink:href="/svg-icons/sprites/icons.svg#olymp-calendar-icon" />
                  </svg>
                </span> Joined {{ joinedDate }}
              </a>
            </li>
            <li v-if="business_name === null ">
              <router-link :to="{name: 'AccountInfo'}">
                <span style="font-size:20px;font-weight:normal;position:relative;top:2px">+</span> Add a Business Name
              </router-link>
            </li>
            <li v-else-if="business_name !== null && country === null ">
              <router-link :to="{name: 'AccountInfo'}">
                <span style="font-size:20px;font-weight:normal;position:relative;top:2px">+</span> Add a Country
              </router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <div v-if="signedIn === true && $route.params.username === getUsername " class="ui-block">
      <div class="friend-item">
        <div class="friend-header-thumb">
          <img src="/image/friend-groups-bg.png" alt="friend" />
        </div>

        <div class="friend-item-content">
          <div class="friend-avatar">
            <div class="author-thumb">
              <img :src="getAvatarLocal()" alt="author" />
            </div>
            <div class="author-content">
              <router-link :to=" '/profile/' + $store.state.user.username" class="h5 author-name">@{{ $store.state.user.username }}</router-link>
              <div class="country" v-if="$store.state.user.club === null ">No Club</div>
              <div class="country" v-else>{{$store.state.user.club}}</div>
            </div>
          </div>

          <div class="swiper-container" data-slide="fade">
            <div class="swiper-wrapper">
              <div class="swiper-slide">
                <div class="friend-count" data-swiper-parallax="-500">
                  <a href="#" class="friend-count-item">
                    <div class="h6">{{ getUserContributions.reviews }}</div>
                    <div class="title">Reviews</div>
                  </a>
                  <a href="#" class="friend-count-item">
                    <div class="h6">{{ getUserContributions.followings }}</div>
                    <div class="title">Following</div>
                  </a>
                  <a href="#" class="friend-count-item">
                    <div class="h6">{{ getUserContributions.followers }}</div>
                    <div class="title">Followers</div>
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>

      </div>
    </div>

    <div class="ui-block" style="max-height: 400px;overflow: auto;">
      <div class="ui-block-content">
        <Timeline id="matchdaydeals" sourceType="profile" :options="{ tweetLimit: '8' }" />
      </div>
    </div>
    <div class="ui-block">
      <div class="ui-block-content">
        <div class="widget w-about">
          <a href="/" class="logo">
            <div class="img-wrap">
              <img src="/image/llogo.png" width="100" alt="Olympus" />
            </div>
            <div class="title-block">
              <h6 class="logo-title">{{projectName}}</h6>
              <div class="sub-title">SOCIAL NETWORK FOR FOOTBALL FANS</div>
            </div>
          </a>
          <p>Matchdaydeals is everything around a matchday and the deals available. From travel, to hotel bookings, to restaurants, to transport, to betting, to car rentals. It is the go-to site for football fans looking for deals around matchday.</p>
          <ul class="socials">
            <li>
              <a href="https://www.instagram.com/matchdaydeals" target="_blank">
                <i class="fab fa-instagram" aria-hidden="true"></i>
              </a>
            </li>
            <li>
              <a href="https://www.facebook.com/matchdaydeals" target="_blank">
                <i class="fab fa-facebook-square" aria-hidden="true"></i>
              </a>
            </li>
            <li>
              <a href="https://www.twitter.com/matchdaydeals" target="_blank">
                <i class="fab fa-twitter" aria-hidden="true"></i>
              </a>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Timeline } from "vue-tweet-embed";
import ls from "../../localstorage";
import avta from "@/functions/getAvatar";
import {mapState} from "vuex";

var pageData = {
  projectName: "",
  userData: {},
  id: null,
  avatar: "",
  signedIn: false,
  joinedDate: '',
  isOtherUser: false,
  city: '',
  country: '',
  business_name: '',
};

export default {
  name: "LeftSideBar",
  data() {
    return pageData;
  },
  components: { Timeline },
  computed: mapState({
    getAvatar: state => state.user.avatar,
    getUsername: state=> state.user.username,
    getUserContributions: state => state.contributions,
  }),
  methods: {
    getAvatarLocal() {
      // let userData = localStorage.fansData;
      return avta({state:{ av: this.getAvatar, us: this.getUsername }});
    },
    checkUserData(userData) {
      if (userData !== "" || userData !== "undefined") {
        this.userData = JSON.parse(userData);
        this.signedIn = true;
      }
    },
    getUserDetails() {
      var usernameRoute = this.$route.params.username;
      if (this.signedIn === false ) {
        this.axios.get('/other/detailsout/' + usernameRoute  + "/1")
                .then(response => {
                  var responseData = response.data.data;
                  // console.log(responseData)
                  this.joinedDate = this.$getDateYear(responseData.join_date,{timestamp: true});
                })
                .catch(error => {
                  // console.log(error)
                  return error;
                })
      }else {
        this.axios.get('/other/detailsin/' + usernameRoute  + "/1")
                .then(response => {
                  var responseData = response.data.data;
                  this.joinedDate = this.$getDateYear(responseData.join_date,{timestamp: true});
                  this.city = responseData.city;
                  this.country = responseData.country;
                  this.business_name = responseData.business_name;
                })
                .catch(error => {
                  // console.log(error)
                  return error;
                })
      }
    },
  },
  mounted() {
    var userData = localStorage.fansData;
    if (userData) {
      this.checkUserData(userData);
    }
    this.getUserDetails();
    var usernameRoute = this.$route.params.username;
    var username = ls.getStorage("fansData");
    var path = this.$route.path;
    this.projectName = this.$store.state.projectName;

    if (this.signedIn === true) {
      if (username[0].username != usernameRoute) {
        if(path === "/profile/settings/accountinfo" || path === "/profile/settings/passwordchange") {
          this.isOtherUser = false;
        }else {
          this.isOtherUser = true;
        }
      } else {
        this.isOtherUser = false;
      }
    } else {
      this.isOtherUser = true;
    }
  }
};
</script>
<style>
.friend-avatar .author-thumb img {
  border: 4px solid #fff;
  display: block;
  width: 98px;
  height: 98px;
}
.friend-count {
  margin-bottom: 0 !important;
}
.friend-item .swiper-container {
  padding-bottom: 0 !important;
}
.dd {
  color: #00AEEF;
  font-weight: bold;
}
.dd span {
  position: relative;
  top: -9px;
  left: 8px;
}
</style>
